@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background-color: #828781;
}

.Header_header__3dpsp {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background: linear-gradient(45deg, #00c6fb, #005bea);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.Header_headerImage__1WTey {
  width: 100%;
  height: 35rem;
  z-index: 0;
}

.Header_headerImage__1WTey img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-2rem);
}

